body {
  margin: 0;
  background-color: #e5e5e5;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "OpenSans";
  src: local(opensans), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: "OpenSans-Bold";
  src: local(opensans-bold), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: "OpenSans-Light";
  src: local(opensans-light), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: light;
  font-style: normal;
  font-stretch: normal;
}
.button,
.advantages-list__slogan,
.feedback__author,
.products__name,
.advantages__investigation__wrapper h2 {
  font-family: "OpenSans-Bold", "Arial", sans-serif;
}
.posting__text {
  font-family: "OpenSans", "Arial", sans-serif;
}
.page-header {
  width: 100%;
}
.page-header__logo {
  cursor: pointer;
}
.page-header__logo g {
  fill: white;
}
.page-header__logo g:hover {
  fill: #d22856;
}
.page-header__logo g:active {
  fill: rgba(255, 255, 255, 0.3);
}
.main-nav {
  position: relative;
  width: 100%;
  margin: 0 auto;
  background-color: #283645;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 11px;
  z-index: 2;
  display: block;
  width: 69px;
  height: 69px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 20px;
  right: 9px;
  width: 52px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 0 0 rgba(255, 255, 255, 0.3), 0 20px 0 0 rgba(255, 255, 255, 0.3);
}
.main-nav--opened .main-nav__toggle::after,
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 35px;
  right: 10px;
  width: 34px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:hover::before {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::after,
.main-nav--opened .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 320px;
}
.main-nav--closed .site-logo {
  border: none;
}
.main-nav--closed {
  background: url("../img/bg-back-mobile-header@1x.jpg") no-repeat 50% 0;
}
.main-nav--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #283645;
}
@media (max-width: 959px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .site-list__item {
  display: block;
}
@media (min-width: 660px) {
  .main-nav {
    width: 100%;
    margin: 0 auto;
  }
  .main-nav__wrapper {
    width: 660px;
  }
  .main-nav__toggle {
    top: 15px;
  }
  .main-nav--closed {
    background: url("../img/bg-back-tablet-header@1x.jpg") no-repeat 50% 0;
  }
}
@media (min-width: 960px) {
  .main-nav {
    width: 100%;
    margin: 0 auto;
  }
  .main-nav__wrapper {
    flex-direction: row;
    justify-content: space-between;
    width: 960px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav--closed {
    background: url("../img/bg-back-desktop-header@1x.jpg") no-repeat 50% 0;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list a {
  display: block;
  padding: 25px;
  font-size: 18px;
  line-height: 12px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: white;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.site-list a[href]:hover {
  color: #d22856;
}
.site-list a[href]:active {
  color: #d22856;
  opacity: 0.7;
}
@media (min-width: 960px) {
  .site-list {
    display: flex;
    justify-content: space-between;
    min-width: 650px;
  }
  .site-list__item {
    position: relative;
    min-width: 150px;
  }
  .site-list a {
    padding: 48px 0;
    font-weight: normal;
    text-transform: none;
    text-align: left;
    border-bottom: none;
  }
  .site-list__item--active::after {
    position: absolute;
    content: "";
    top: 70%;
    width: 45%;
    height: 2px;
    background: white;
  }
  .site-list__item--active:nth-child(2)::after {
    width: 70%;
  }
  .site-list__item--active:nth-child(4)::after {
    width: 85%;
  }
  .site-list__item:hover::after {
    position: absolute;
    content: "";
    top: 70%;
    width: 45%;
    height: 2px;
    background: #d22856;
  }
  .site-list__item:nth-child(2):hover::after {
    width: 70%;
  }
  .site-list__item:nth-child(4):hover::after {
    width: 85%;
  }
}
.site-logo {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 20px;
  padding-bottom: 12px;
  box-sizing: border-box;
  border-bottom: 1px solid #000000;
}
.page-header__logo-pink-white-tablet,
.page-header__logo-pink-white-desktop {
  display: none;
}
.page-header__logo-pink-white-mobile,
.page-header__logo-pink-white-tablet,
.page-header__logo-pink-white-desktop {
  fill: white;
}
.page-header__logo-pink-white-mobile:hover,
.page-header__logo-pink-white-tablet:hover,
.page-header__logo-pink-white-desktop:hover {
  fill: #d22856;
}
.page-header__logo-pink-white-mobile:active,
.page-header__logo-pink-white-tablet:active,
.page-header__logo-pink-white-desktop:active {
  fill: grey;
}
@media (min-width: 660px) {
  .site-logo {
    min-height: 100px;
    padding-top: 0;
  }
  .site-logo__item {
    margin-top: 20px;
  }
  .page-header__logo-pink-white-mobile {
    display: none;
  }
  .page-header__logo-pink-white-tablet {
    display: block;
    margin-top: 25px;
  }
}
@media (min-width: 960px) {
  .page-header__logo-pink-white-tablet {
    display: none;
  }
  .page-header__logo-pink-white-desktop {
    display: block;
    margin-top: 30px;
  }
  .site-logo__item {
    margin-top: 25px;
  }
}
.breaking-description {
  padding: 15px;
}
.breaking-about-me {
  padding: 15px;
}
.breaking-gallery {
  padding: 15px;
}
.breaking-shedule {
  padding: 15px;
}
.breaking-footer {
  padding: 15px;
}
.breaking-gallery__list {
  margin: 0;
  padding: 0;
  list-style: none;
